import { Component, Suspense, lazy } from "react";
import { Route, NavLink, Switch } from "react-router-dom";
import Loader from "../components/Loader/Loader";
import styles from "../views/movieDetail.module.css";
import { fetchMoviesDetails } from "../services/moviesApi";
import noImage from "../images/noImage.jpg";
import routes from "../util";

const reviewComponent = lazy(() => import("../components/Reviews/Reviews"));
const castComponent = lazy(() => import("../components/Cast/Cast"));

const imagesUrl = "https://image.tmdb.org/t/p/w500";
class MovieDetailsPage extends Component {
  state = {
    title: null,
    release_date: "",
    vote_average: null,
    overview: null,
    genres: [],
    poster_path: null,
  };
  async componentDidMount() {
    const movieId = this.props.match.params.movieId;
    fetchMoviesDetails(movieId).then(
      ({ title, vote_average, overview, genres, poster_path }) =>
        this.setState({
          title,
          vote_average,
          overview,
          genres,
          poster_path,
        })
    );
  }
  render() {
    const { match } = this.props;
    const { title, vote_average, overview, genres, poster_path } = this.state;
    const userScore = vote_average * 10;
    return (
      <div>
        <h1>{title}</h1>
        <div>
          <div>
            {poster_path ? (
              <img
                src={`${imagesUrl}${poster_path}`}
                alt={title}
                height="250px"
              />
            ) : (
              <img src={noImage} alt={title} />
            )}
          </div>
          <div>
            <p>User Score: {userScore}%</p>
            {overview && (
              <>
                <h2>Overview</h2>
                <p>{overview}</p>
              </>
            )}
            {genres.length > 0 && (
              <>
                <h2>Genres</h2>
                <p>
                  {genres.map((genre) => (
                    <span key={genre.name}>{genre.name}</span>
                  ))}
                </p>
              </>
            )}
          </div>
        </div>
        <h2>Aditional information</h2>
        <NavLink
          to={{
            pathname: `${match.url}${routes.cast}`,
            state: { from: this.props.location },
          }}
          className={styles.link}
          activeClassName={styles.activeLink}
        >
          Cast
        </NavLink>
        <NavLink
          to={{
            pathname: `${match.url}${routes.reviews}`,
            state: {
              from: this.props.location,
            },
          }}
          className={styles.link}
          activeClassName={styles.activeLink}
        >
          {""}
          Reviews{""}
        </NavLink>
        <Suspense fallback={<Loader />}>
          <Switch>
            <Route
              path={`${match.path}${routes.cast}`}
              component={castComponent}
            />
            <Route
              path={`${match.path}${routes.reviews}`}
              component={reviewComponent}
            />
          </Switch>
        </Suspense>
      </div>
    );
  }
}
export default MovieDetailsPage;
